import React from "react";
import { connect } from "react-redux";
import { handleMenu } from "../redux/actions/globalActions";
import NavItem from "./NavItem";
import FacebookIcon from "@material-ui/icons/Facebook";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import LockIcon from "@material-ui/icons/Lock";
import LinkIcon from "@material-ui/icons/Link";
import CallIcon from "@material-ui/icons/Call";

const SideNav = ({ handleMenu, menuIsOpen, themeIsLight }) => {
  return (
    <nav
      className={`sidenav ${menuIsOpen ? " sidenav--isOpen" : ""} ${
        themeIsLight ? "" : "sidenav--isDark"
      }`}
    >
      <ul className='sidenav__list'>
        <NavItem
          name='Portfolio'
          link='https://mstdev89.github.io/portfolio/'
          icon={<LinkIcon />}
          menu={true}
        />
        <NavItem
          name='Contact'
          link='/contact'
          icon={<CallIcon />}
          menu={true}
        />
        <NavItem name='Admin' link='/admin' icon={<LockIcon />} menu={true} />
      </ul>
    </nav>
  );
};

const mapStateToProps = (state) => ({
  menuIsOpen: state.global.menuIsOpen,
  themeIsLight: state.global.themeIsLight,
});

export default connect(mapStateToProps, { handleMenu })(SideNav);
